// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import 'todomvc-app-css/index.css'

Vue.config.productionTip = false

/* eslint-disable no-new */
let app = new Vue({
  el: '#app',
  data: {
    newTodo: '',
    todos: [{
      index: 0,
      title: '学习Javascript',
      completed: false,
    },{
      index: 1,
      title: '学习vue',
      completed: true,
    }],
    showTodos: null,
    showTodosType: 'all',
    editedTodo: null,
    editCache: null,
  },
  created() {
    this.showTodos = this.allTodos;
  },
  watch: {
    showTodosType() {
      this.switchShowType();
    },
    todos() {
      this.switchShowType();
    },
  },
  computed: {
    remain() {
      return this.activeTodos.length;
    },
    allTodos() {
      return this.todos;
      
    },
    activeTodos() {
      return this.todos.filter(todo => {
        return todo.completed === false;
      });
    },
    completedTodos() {
      return this.todos.filter(todo => {
        return todo.completed === true;
      });
    },
  },
  methods: {
    addTodo() {
      if(this.newTodo.length > 0) {
        this.todos.push({
          index: this.todos.length+1,
          title: this.newTodo,
          completed: false,
        });
        this.newTodo = '';
      }
    },
    deleteTodo(index) {
      let i = this.todos.findIndex(e => {
        return e.index === index;
      });
      this.todos.splice(i, 1);
    },
    clearCompleted() {
      this.todos = this.activeTodos
    },
    switchShowType() {
      switch(this.showTodosType) {
        case 'all': {
          this.showTodos = this.allTodos;
          break;
        }
        case 'active': {
          this.showTodos = this.activeTodos;
          break;
        }
        case 'completed': {
          this.showTodos = this.completedTodos;          
          break;
        }
      }
    },
    editTodo(todo) {
      this.editCache = todo.title;
      this.editedTodo = todo;
    },
    commitEdit(todo) {
      this.editedTodo = null;
      this.editCache = null;
      if(!todo.title || todo.title.length < 1) {
        this.deleteTodo(todo.index)
      } else {
        let i = this.todos.findIndex(e => {
          return e.index === todo.index;
        });
        this.todos[i].title = todo.title;
      }
    },
    cancelEdit(todo) {
      todo.title = this.editCache;
      this.editedTodo = null;
      this.editCache = null;
    }
  },
  directives: {
    focus(el, value) {
      if(value) {
        el.focus();
      }
    }
  },
})



